@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2.no_bookings {
  margin: 40px auto;
  font-family: "Ubuntu", sans-serif;
  font-size: 30px;
  font-weight: bold;
  color: #121212;
  text-align: center;
  letter-spacing: 5px;
  text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3),
    0px -4px 10px rgba(255, 255, 255, 0.3);
}

.list-group {
  width: 100%;
  height: 220px;
  overflow-y: scroll;
}

.list-group-item {
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.list-group-item span {
  width: 80%;
  font-size: 20px;
}

.list-group-item svg {
  font-size: 48px;
  background-color: #17a2b8;
  border-radius: 50%;
  border: 2px solid #454558;
  padding: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

@media screen and (max-width: 430px) {
  .list-group-item span {
    font-size: 15px;
  }

  .list-group-item svg {
    font-size: 40px;
  }
}

.list-group-item svg:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.slots_Card {
  /* height: 300px; */

  /* margin: 0 auto !important; */
}

.slots_Card .card_body > div.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.slots_Card .card_body h5 {
  color: #17a2b8;
  font-weight: bold;
  text-align: center;
  text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3),
    0px -4px 10px rgba(255, 255, 255, 0.3);
}

.slots_Card .card_body > div.row > button {
  /* background-color: #17a2b8; */
  padding: 5px;
  width: 125px !important;
  height: 93px;
  margin: 7px 8px !important;
  /* border: 2px solid #17a2b8; */
  border-radius: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.slots_Card .card_body > div.row > button:active {
  /* box-shadow: 0 0 0 0.2rem rgb(222 170 12 / 50%); */
  border: 2px solid #1a1918 !important;
}
.slots_Card .card_body > div.row > button:focus {
  border: 2px solid #343a40 !important;
  background-color: #17a2b8 !important;
}

.slots_Card .card_body > div.row > button.buttonActive {
  border: 2px solid #343a40 !important;
  background-color: #17a2b8 !important;
}

/* .slots_Card .card_body > div.row > div > div {
} */

table.showBookings {
  font-size: 15px;
}

table.showBookings td,
table.showBookings th {
  vertical-align: middle !important;
}

@media screen and (max-width: 450px) {
  table.showBookings {
    font-size: 12.5px;
  }
  table.showBookings td {
    padding: 3px;
  }
  table.showBookings td.date {
    min-width: 100px !important;
  }
  table.showBookings td.time {
    min-width: 64px !important;
  }
}

.slots_nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.slots_nav small {
  font-size: 16px;
  display: block;
}

.slots_nav small span {
  color: #dae0e5 !important;
  cursor: pointer;
}
.slots_nav button {
  margin: 0 5px;
}

@media screen and (max-width: 480px) {
  .slots_nav small {
    font-size: 14px;
  }
}

.bottom_card {
  background-color: #343a40 !important;
}

@media screen and (max-width: 480px) {
  .select {
    font-size: 14px;
  }
}

.showslotsTiming {
  /* background-color: #2c383a; */
}

.showslotsTiming .table-responsive {
  /* height: 250px !important; */
}

.showslotsTiming table tbody tr td.no_data {
  font-size: 20px;
}

@media screen and (max-width: 480px) {
  .showslotsTiming table tbody tr td.no_data {
    font-size: 13px;
  }
}

.btn:focus:active {
  background-color: #19682c;
}
.btn:focus {
  outline: 0;
  box-shadow: none !important;
}

.logo {
  width: 85px;
  height: 50px;
}

.navbarLinks {
  margin-left: 10px !important;
}

.users_heading {
  margin: 20px;
}
.users_heading h2 {
  color: #17a2b8;
  font-family: "Raleway", sans-serif;
  font-size: 45px;
  font-weight: 800;
  line-height: 72px;
  margin: 0 0 0px;
  text-align: center;
  text-transform: uppercase;
  text-shadow: -1px 2px 5px #17a3b850, 0px -1px 10px #272929a8;
}

@media screen and (max-width: 550px) {
  .logo {
    width: 71px;
    height: 42px;
  }
  .users_heading h2 {
    font-size: 27px;
    font-weight: 800;
    line-height: 30px;
  }
  .navbarLinks {
    margin-left: 0px !important;
  }
}

table thead {
  border-radius: 10px;
}
.navbar-brand {
  font-size: 20px;
  margin-right: 0 !important;
}

.card_body a:hover {
  text-decoration: none;
}

.users_heading p {
  color: #212529;
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 800;
  margin: 4px 0 0px;
  text-align: center;
  text-transform: capitalize;
}

.users_heading p span {
  color: #cad0d7;
  cursor: pointer;
}

.users_heading h2::selection,
.users_heading p::selection,
.users_heading span::selection {
  color: none;
  background: none;
}

@media screen and (max-width: 450px) {
  .users_heading p {
    margin: 4px 0 0px;
  }
}

.tableDiv {
  height: 470px !important;
  overflow-y: auto;
}

table.allBookings {
  font-size: 15px;
}

table.allBookings td,
table.allUsers td,
table.allBookings th,
table.allUsers th {
  vertical-align: middle !important;
}

@media screen and (max-width: 450px) {
  table.allBookings,
  table.allUsers {
    font-size: 12.3px !important;
  }
  table.allBookings td {
    padding: 5px;
  }
  table.allUsers td{
    padding: 8px;
  }
  table.allBookings td.date {
    min-width: 100px !important;
  }
  table.allBookings td.time {
    min-width: 64px !important;
  }
  table.allBookings td.duration {
    min-width: 100px !important;
  }
}

body {
  font-family: "Merriweather Sans", sans-serif !important;
  background-color: #18454ac0;
}

.main_heading {
  margin-bottom: 20px;
}

.card_body {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

